import axios from 'axios';
import { useContext, useEffect, useMemo, useState } from 'react';
import { Controller, useForm } from 'react-hook-form';
import { Input, Textarea, CheckboxRadio, Selectbox } from './FormElement';
import {
  MessageContext,
  handleSuccessMessage,
  handleErrorMessage,
} from '../store/messageStore';

function ProductModal({
  closeProductModal,
  getProducts,
  type,
  tempProduct,
  categoryList,
}) {
  const initData = useMemo(
    () => ({
      title: '',
      category: '',
      origin_price: 500,
      price: 300,
      unit: '',
      description: '',
      content: '',
      is_enabled: 1,
      imageUrl: '',
    }),
    []
  );
  const [tempData, setTempData] = useState(initData);
  const [uploadImgMsg, setUploadImgMsg] = useState('');
  const [uploadImgVal, setUploadImgVal] = useState('');

  const {
    register,
    handleSubmit,
    reset,
    watch,
    setValue,
    control,
    formState: { errors },
  } = useForm({
    defaultValues: tempData,
    mode: 'onTouched',
  });
  const imageUrl = watch('imageUrl');
  const originPrice = watch('origin_price');
  const validatePrice = (value) => {
    if (value && originPrice && Number(value) >= Number(originPrice)) {
      return '';
    }
    return true;
  };
  const [, dispatch] = useContext(MessageContext);

  useEffect(() => {
    if (type === 'create') {
      setTempData(initData);
      console.log('create', tempData);
    } else if (type === 'edit') {
      setTempData((pre) => ({ ...pre, ...tempProduct }));
    }
    setUploadImgMsg('');
    setUploadImgVal('');
  }, [type, tempProduct, initData]);

  useEffect(() => {
    const resetForm = () => {
      reset(tempData);
    };
    resetForm();
  }, [tempData, reset]);

  const uploadImg = async (e) => {
    setUploadImgVal(e.target.value);
    const file = e.target.files[0]; // 
    const formData = new FormData(); // FormData
    formData.append('file', file); // FormData
    if (file.type.split('/')[0] !== 'image') {
      setUploadImgMsg(' jpg  png ');
      return;
    }
    try {
      setUploadImgMsg('...');
      const res = await axios.post(
        `/v2/api/${process.env.REACT_APP_API_PATH}/admin/upload`,
        formData, // FormData
        {
          headers: {
            'Content-Type': 'multipart/form-data', // Content-Type
          },
        }
      );
      if (res.data.success) {
        setValue('imageUrl', res.data.imageUrl);
        // setTempData((pre) => ({
        //   ...pre,
        //   uploadImg: e.target.value,
        //   imageUrl: res.data.imageUrl,
        // }));
        setUploadImgMsg('');
      }
    } catch (error) {
      console.log(error);
      setUploadImgMsg('');
    }
  };
  const submit = async (data) => {
    try {
      let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product`;
      let method = 'post';
      if (type === 'edit') {
        //api
        api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${tempProduct.id}`;
        method = 'put';
      }
      const res = await axios[method](api, {
        data: data,
      });
      if (res.data.success) {
        handleSuccessMessage(dispatch, res);
        closeProductModal();
        getProducts();
      }
    } catch (error) {
      console.log(error);
      handleErrorMessage(dispatch, error);
    }
  };
  return (
    <div
      className='modal fade'
      tabIndex='-1'
      id='productModal'
      aria-labelledby='productModalLabel'
      aria-hidden='true'
    >
      <div className='modal-dialog modal-lg'>
        <div className='modal-content'>
          <div className='modal-header'>
            <h1 className='modal-title fs-5' id='productModalLabel'>
              {type === 'create' ? '' : ` ${tempData.title}`}
            </h1>
            <button
              type='button'
              className='btn-close'
              aria-label='Close'
              onClick={closeProductModal}
            />
          </div>
          <div className='modal-body'>
            <form onSubmit={handleSubmit(submit)} className='row'>
              <div className='col-sm-4'>
                <div className='form-group mb-3'>
                  <Input
                    register={register}
                    errors={errors}
                    id='imageUrl'
                    type='text'
                    labelText='*'
                    placeholder=''
                    rules={{
                      required: {
                        value: true,
                        message: '',
                      },
                    }}
                    onChange={(e) =>
                      setTempData((pre) => ({
                        ...pre,
                        imageUrl: e.target.value,
                      }))
                    }
                  />
                </div>

                <div className='form-group mb-3'>
                  <label className='w-100' htmlFor='customFile'>
                     
                    <input
                      type='file'
                      id='customFile'
                      className='form-control mb-2'
                      value={uploadImgVal}
                      onChange={uploadImg}
                    />
                  </label>
                  <p className='text-muted'>{uploadImgMsg}</p>
                </div>
                <img
                  className={imageUrl ? 'img-fluid' : 'd-none'}
                  src={imageUrl}
                  alt='product_mainImg'
                />
                <img src='' alt='' className='img-fluid' />
              </div>
              <div className='col-sm-8'>
                <div className='form-group mb-3'>
                  <Input
                    register={register}
                    errors={errors}
                    id='title'
                    type='text'
                    labelText='*'
                    placeholder=''
                    rules={{
                      required: {
                        value: true,
                        message: '',
                      },
                    }}
                  />
                </div>
                <div className='row'>
                  <div className='form-group mb-3 col-md-6'>
                    <Selectbox
                      Controller={Controller}
                      control={control}
                      data={categoryList}
                      labelText='*'
                      id='category'
                      placeholder=''
                      rules={{
                        required: {
                          value: true,
                          message: '',
                        },
                      }}
                    />
                  </div>
                  <div className='form-group mb-3 col-md-6'>
                    <Input
                      register={register}
                      errors={errors}
                      id='unit'
                      type='unit'
                      labelText='*'
                      placeholder=''
                      rules={{
                        required: {
                          value: true,
                          message: '',
                        },
                      }}
                    />
                  </div>
                </div>
                <div className='row'>
                  <div className='form-group mb-3 col-md-6'>
                    <Input
                      register={register}
                      errors={errors}
                      id='origin_price'
                      type='number'
                      labelText='*'
                      placeholder=''
                      rules={{
                        required: {
                          value: true,
                          message: '',
                        },
                        valueAsNumber: {
                          value: true,
                          message: '',
                        },
                      }}
                    />
                  </div>
                  <div className='form-group mb-3 col-md-6'>
                    <Input
                      register={register}
                      errors={errors}
                      id='price'
                      type='number'
                      labelText='*'
                      placeholder=''
                      rules={{
                        required: {
                          value: true,
                          message: '',
                        },
                        validate: validatePrice,
                        valueAsNumber: {
                          value: true,
                          message: '',
                        },
                      }}
                    />
                  </div>
                </div>
                <hr />
                <div className='form-group mb-3'>
                  <Textarea
                    register={register}
                    errors={errors}
                    labelText=''
                    id='description'
                    placeholder=''
                    rowNum={2}
                  />
                </div>
                <div className='form-group mb-3'>
                  <Textarea
                    register={register}
                    errors={errors}
                    labelText=''
                    id='content'
                    placeholder=''
                    rowNum={4}
                  />
                </div>
                <div className='form-group mb-3'>
                  <CheckboxRadio
                    register={register}
                    errors={errors}
                    type='checkbox'
                    name='is_enabled'
                    id='is_enabled'
                    labelText=''
                    hasErrorMsg={false}
                  />
                </div>
              </div>
              <div className='modal-footer'>
                <button
                  type='button'
                  className='btn btn-secondary'
                  onClick={closeProductModal}
                >
                  
                </button>
                <button type='submit' className='btn btn-primary'>
                  
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProductModal;
